@import "variables";

.main {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  @media screen and (max-width: 1023px) {
    width: 100%;
  }
  .slider {
    padding: 0 20px;
    position: relative;
    @media screen and(min-width: 768px)and (max-width: 1023px) {
      padding: 0;
      width: 100%;
      //max-width: 944px;
      height: 490px;
    }
    @media screen and (max-width: 416px) {
      //padding: 0;
      //width: 100%;
      ////max-width: 944px;
      height: 188px;
      padding: 0;
    }
    @media screen and (min-width: 416px) and (max-width: 767px){
      //padding: 0;
      //width: 100%;
      ////max-width: 944px;
      height: 330px;
      padding: 0;
    }
    .owl-carousel {
      z-index: 0;
      height: 100%;
      .owl-nav {

        .owl-prev {
          background: url(../images/array.png) no-repeat;
          position: absolute;
          top: 0;
          left: 0;
          width: 50px;
          height: 100%;
          background-position-y: center;
          background-position-x: center;
        }
        .owl-next {
          background: url(../images/array.png) no-repeat;
          position: absolute;
          top: 0;
          right: 0;
          width: 50px;
          height: 100%;
          -ms-transform: rotate(180deg);
          transform: rotate(180deg);
          -webkit-transform: rotate(180deg);
          background-position: center center;
        }
      }
      .item {
        cursor: pointer;
        @media screen and (max-width: 1023px) {
          width: 100%;
          height: 100%;
        }
        img {
          //width: 100%;
          display: block;
          width: 100%;
          @media screen and (max-width: 1023px) {
            height: 100%;
          }
        }
      }
    }
    .owl-carousel .owl-stage, .owl-carousel.owl-drag .owl-item{
      -ms-touch-action: auto;
      touch-action: auto;
      height: 100%;
    }
    .owl-stage-outer {
      height: 100%;
      @media screen and (max-width: 1024px) {
      //display: none;
      }
    }
    .owl-stage {
      height: 100%;
    }
    &-footer {
      display: flex;
      flex-direction: row;
      position: relative;
      padding: 0 40px;
      @media screen and (max-width: 1023px) {
        width: 100%;
        padding: 0;
        height: 85px;
      }
      &_delivery {
        height: 170px;
        width: 600px;
        background-color: #F7F6F2;
        padding-bottom: 10px;
        font-family: $font_ubuntu;
        position: relative;

        a {
          text-decoration: none;
        }
        @media screen and (max-width: 1024px) {
          width: 50%;
          height: auto;
          padding: 5% 0;
          background-color: #F7F6F2;
        }
        &_main-text {
          color: #EBE8DD;
          font-size: 90px;
          font-weight: bold;
          line-height: 90px;
          text-align: center;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          @media screen and (max-width: 1024px) {
            font-size: 24px;
            line-height: 24px;
          }
        }
        &_secondary-text {
          position: absolute;
          top: 57%;
          left: 50%;
          transform: translate(-50%, -57%);
          width: 287px;
          color: #231F20;
          font-size: 18px;
          line-height: 24px;
          text-align: center;
          @media screen and (max-width: 1024px) {
            font-size: 12px;
            line-height: 18px;
            width: 120px;
            //position: relative;
            z-index: 5;
          }
        }
        &_secondary-text_preorder {
          position: absolute;
          top: 57%;
          left: 50%;
          transform: translate(-50%, -57%);
          width: 227px;
          color: #231F20;
          font-size: 18px;
          line-height: 24px;
          text-align: center;
          @media screen and (max-width: 1024px) {
            font-size: 12px;
            line-height: 18px;
            width: 120px;
            //position: relative;
            z-index: 5;
          }
        }
        &_moreinfo-text {
          display: flex;
          width: auto;
          align-items: center;
          top: 90%;
          position: absolute;
          left: 50%;
          transform: translate(-50%, -50%);
          cursor: pointer;
          @media screen and (max-width: 1024px) {
            display: none;
          }
          &_left-dash, &_right-dash {
            height: 2px;
            width: 21px;
            background-color: #CBBC9D;
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
          }
          &_text {
            margin: 0 10px;
            color: #231F20;
            font-size: 16px;
            font-weight: bold;
            line-height: 16px;
            text-align: center;
            text-transform: uppercase;

            a {
              text-decoration: none;
              color: inherit;
            }
          }
        }
        &_img {
          position: absolute;
          bottom: 0;
          right: 65px;
          @media screen and (max-width: 1024px) {
            right: 50%;
            transform: translateX(50%);
            width: 80px;
            z-index: 0;
            img {
              width: 80px;
            }
          }
          &_right {
            position: absolute;
            top: 0;
            right: 133px;
            @media screen and (max-width: 1024px) {
              right: 50%;
              transform: translateX(50%);
              width: 80px;
              z-index: 0;
              img {
                width: 80px;
              }
            }
          }
        }
      }
      &_rectangle {
        background: url(../images/Rectangle2.png) no-repeat;
        height: 2px;
        width: 101px;
        transform: rotate(135deg);
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        position: absolute;
        top: 50%;
        left: 46%;
        @media screen and (max-width: 1024px) {
          //display: none;
          left: 47%;
          width: 28px;
        }
      }
    }
  }
  .main_map {
    padding: 40px 40px 0;
    position: relative;
    @media screen and (max-width: 1024px) {
      padding: 0;
      margin-top: 40px;
    }
    &_buttons {
      position: absolute;
      width: 187px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      top: 50%;
      left: 40px;
      transform: translateY(-50%);
      font-family: $font_ubuntu;
      @media screen and (max-width: 1024px) {
        position: absolute;
        top: 20px;
        left: 0;
        flex-direction: row;
        justify-content: space-between;
        width: calc(100% - 30px);
        height: 40px;
        background-color: #231f20;
        padding: 0 15px;
      }
      &_button {
        margin-bottom: 10px;
        border-radius: 2px;
        cursor: pointer;
        width: 147px;
        color: #FFFFFF;
        @media screen and (max-width: 1024px) {
          margin-bottom: 0;
          border-radius: 0;
          cursor: pointer;
          width: auto;
          color: #FFFFFF;
        }
        &_text {
          padding: 12px 0 14px 20px;
          background-color: rgba(35, 31, 32, 0.8);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
          color: #FFFFFF;
          font-family: $font_ubuntu;
          font-size: 16px;
          line-height: 24px;
          font-weight: normal;
          @media screen and (max-width: 1024px) {
            padding: 14px 0 14px 15px;
            font-size: 11px;
            line-height: 11px;
          }
        }
      }
      .active {
        width: 187px;
        @media screen and (max-width: 1024px) {
          width: auto;
        }
        .main_map_buttons_button_text {
          padding: 17px 0 17px 20px;
          background-color: #FFFFFF;
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
          color: #1A1718;
          @media screen and (max-width: 1024px) {
            padding: 14px 0 11px 0;
            background-color: rgba(35, 31, 32, 1);
            box-shadow: none;
            color: #BFAF8C;
            border-bottom: 4px #CBBC9D solid;
          }
        }
      }
      &_metro {
        position: absolute;
        width: 187px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        top: 50%;
        right: 40px;
        transform: translateY(-50%);
        font-family: $font_ubuntu;
        @media screen and (max-width: 1024px) {
          position: absolute;
          top: 20px;
          left: 0;
          flex-direction: row;
          justify-content: space-between;
          width: calc(100% - 30px);
          height: 40px;
          background-color: #231f20;
          padding: 0 15px;
        }
        &_button {
          margin-bottom: 10px;
          border-radius: 2px;
          cursor: pointer;
          width: 186px;
          color: #FFFFFF;
          @media screen and (max-width: 1024px) {
            margin-bottom: 0;
            border-radius: 0;
            cursor: pointer;
            width: auto;
            color: #FFFFFF;
          }
          &_text {
            padding: 12px 0 14px 20px;
            background-color: rgba(35, 31, 32, 0.8);
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
            color: #FFFFFF;
            font-family: $font_ubuntu;
            font-size: 16px;
            line-height: 24px;
            font-weight: normal;
            @media screen and (max-width: 1024px) {
              padding: 14px 0 14px 15px;
              font-size: 11px;
              line-height: 11px;
            }
          }
          //&_back {
          //  @include sp__phone;
          //}
        }
        .active {
          width: 187px;
          @media screen and (max-width: 1024px) {
            width: auto;
          }
          .main_map_buttons_button_text {
            padding: 17px 0 17px 20px;
            background-color: #FFFFFF;
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
            color: #1A1718;
            @media screen and (max-width: 1024px) {
              padding: 14px 0 11px 0;
              background-color: rgba(35, 31, 32, 1);
              box-shadow: none;
              color: #BFAF8C;
              border-bottom: 4px #CBBC9D solid;
            }
          }
        }

      }

    }
  }
}

