.app_content {
  position: relative;
  height: 710px;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  background-color: #F7F6F2;
  @media screen and (min-width: 768px) and (max-width: 1024px) {
    width: 100%;
    height: 444px;
    padding-bottom: 100px;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    height: 477px;
    //padding-bottom: 100px;
  }
  &_images {
    position: absolute;
    height: 710px;
    right: 0;
    bottom: 0;
    width: 100%;

    @media screen and (max-width: 1024px) {
      height: auto;
    }
    &_bg {
      width: 100%;
      height: 710px;
      position: relative;
      @media screen and (max-width: 1024px) {
        position: absolute;
        bottom: 0;
        right: 0;
        height: auto;
      }
      &-desktop {
        display: block;
        position: absolute;
        bottom: 0;
        right: 0;
        @media screen and (max-width: 1024px) {
          display: none;
        }
      }
      &-mobile {
        display: none;
        @media screen and (max-width: 1024px) {
          display: none;
          position: absolute;
          //display: block;
          width: 80%;
          right: 0;
          bottom: 0;
        }
      }
    }
    &_phones {
      position: absolute;
      right:0;
      bottom: 0;
      width: 55%;
      @media screen and (max-width: 1024px) {
        right: 0;
        width: 100%;
      }
      &-desktop {
        display: block;
        width: 100%;
        //height: 652px;
        @media screen and (max-width: 1024px) {
          display: none;
        }
      }
      &-mobile {
        display: none;
        @media screen and (max-width: 767px) {
          display: block;
          width: 100%;
          max-width: 270px;
          position: absolute;
          //right: 5%;
          bottom: 0;
          left: 50%;
          transform: translate(-50%,0);
        }
        @media screen and(min-width: 768px) and (max-width: 1024px) {
          display: block;
          width: 45%;
          position: absolute;
          right: 5%;
          bottom: 0;
        }
      }
    }
  }
  &_text {
    display: block;
    font-family: $font_ubuntu;
    padding: 212px 0 0 40px;
    position: relative;
    @media screen and (max-width: 1024px) {
      //padding: 10px 20px 0 20px;
      //max-width: 280px;
    }
    @media screen and (max-width: 767px) {
      width: 100%;
      padding: 30px 0 0 0;
      text-align: center;
      //padding-bottom: 100px;
    }
    &_main {
      color: #231F20;
      font-size: 40px;
      font-weight: 500;
      line-height: 44px;
      width: 442px;
      @media screen and (min-width: 768px)and(max-width: 1024px) {
        font-size: 20px;
        line-height: 20px;
        width: 100%;
      }
      @media screen and (max-width: 767px) {
        font-size: 16px;
        line-height: 16px;
        width: 100%;
      }
      &_adver {
        margin-top: 20px;
        color: #231F20;
        font-size: 40px;
        font-weight: 500;
        line-height: 44px;
        position: relative;
        @media screen and (min-width: 768px) and (max-width: 1024px) {
          font-size: 50px;
          line-height: 50px;
          width: 47%;
          //display: none;
        }
        @media screen and (max-width: 767px) {
          width: 100%;
          font-size: 36px;
          line-height: 30px;
          //padding-bottom: 100px;
        }
      }

    }
    &_other {
      color: #7F7873;
      font-size: 26px;
      font-weight: 500;
      line-height: 30px;
      width: 400px;
      padding: 20px 0 30px;
      @media screen and (min-width: 768px) and (max-width: 1024px) {
        font-size: 16px;
        line-height: 26px;
        padding-bottom: 60px;
        font-weight: normal;
        width: 47%;
      }
      @media screen and (max-width: 767px) {
        width: 80%;
        font-size: 13px;
        line-height: 20px;
        text-align: center;
        /* padding: 20px 10px; */
        margin: 0 auto;
        padding-bottom: 0;
      }
    }
    &_download {
      display: flex;
      flex-direction: row;
      @media screen and (max-width: 1024px) {
        flex-direction: column;
        position: absolute;
        bottom: -70px;
      }
      &_google {
        background: url(../images/Playstore.png) center no-repeat;
        background-size: 100%;
        height: 45px;
        width: 150px;
        margin-right: 20px;
        @media screen and (max-width: 1024px) {
          margin-bottom: 10px;
        }
      }
      &_apple {
        background: url(../images/Appstore.png) center no-repeat;
        background-size: 100%;
        height: 45px;
        width: 150px;
      }
    }
    &_logo {
      background: url(../images/logo_N.png) no-repeat;
      height: 212px;
      width: 325px;
      opacity: 0.2;
      position: absolute;
      top: 212px;
      @media screen and (max-width: 1024px) {
        display: none;
      }
    }
    &_url {
      color: #7F7873;
      font-size: 16px;
      line-height: 18px;
      margin-top: 145px;
      @media screen and (max-width: 1024px) {
        display: none;
      }
      a {
        color: #332C26;
        text-decoration: none;
      }
    }
  }
}

.about_content {
  position: relative;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  @media screen and (max-width: 1024px) {
    width: 100%;
  }
  &_title {
    position: relative;
    font-family: $font_ubuntu;
    font-weight: 500;
    font-size: 40px;
    z-index: 1;
    line-height: 44px;
    margin: 0 auto;
    width: 640px;
    padding: 50px 0 0 0;
    @media screen and (max-width: 1024px) {
      width: 60%;
      margin: 0 20px;
      font-size: 24px;
      font-weight: 500;
      line-height: 26px;
    }
  }
  &_footer-grey {
    position: absolute;
    height: 273px;
    width: 61px;
    bottom: 0;
    background-color: #F7F6F2;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    z-index: 0;
    @media screen and (max-width: 1024px) {
      display: none;
    }
  }
  &_column-grey {
    position: absolute;
    top: 0;
    z-index: 0;
    width: 266px;
    height: 100%;
    left: 697px;
    background-color: #F7F6F2;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    @media screen and (max-width: 1024px) {
      display: none;
    }
  }
  &_number-logo {
    background: url(../images/24.png) no-repeat;
    width: 300px;
    height: 206px;
    position: absolute;
    top: 35px;
    left: 81px;
    z-index: 0;
    background-size: contain;
    @media screen and (max-width: 1024px) {
      background-size: cover;
      width: 140px;
      height: 80px;
      left: 20px;
    }
  }
  &_items {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: row;
    padding: 70px 104px 0 253px;
    align-items: flex-start;
    @media screen and (max-width: 1024px) {
      padding: 0;
    }
    &_text {
      width: 400px;
      display: flex;
      flex-direction: column;
      padding-top: 10px;
      @media screen and (max-width: 1024px) {
        width: calc(100% - 40px);
        margin: 0 auto;
      }
      &_header {
        font-size: 26px;
        line-height: 30px;
        font-family: $font_ubuntu;
        color: #7F7873;
        text-align: left;
        font-weight: 500;
        @media screen and (max-width: 1024px) {
          font-size: 16px;
          font-weight: 500;
          line-height: 22px;
        }
      }
      &_description {
        font-size: 16px;
        line-height: 24px;
        font-family: $font_ubuntu;
        color: #7F7873;
        text-align: left;
        padding: 35px 0 40px 0;
        span:first-child {
          padding-bottom: 10px;
          display: block;
        }
        @media screen and (max-width: 1024px) {
          font-size: 13px;
          line-height: 20px;
          padding: 0;
        }
      }
      &_mobile-img {
        display: none;
        @media screen and (max-width: 1024px) {
          display: block;
          img {
            width: 100%;
            margin: 20px auto 20px;
          }
        }
      }
      &_moreinfo {
        display: flex;
        align-items: center;
        font-family: $font_ubuntu;
        text-transform: uppercase;
        cursor: pointer;
        a {
          color: inherit;
          text-decoration: none;
        }
        @media screen and (max-width: 1024px) {
          margin: 20px auto;
        }
        &_left-dash, &_right-dash {
          height: 2px;
          width: 21px;
          background-color: #CBBC9D;
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        }
        &_text {
          margin: 0 10px;
          font-family: $font_ubuntu;
          color: #231F20;
          font-size: 16px;
          font-weight: bold;
          line-height: 16px;
          text-align: center;
          text-transform: uppercase;
        }
      }
    }
    &_img {
      background: url(../images/eating_main.jpg) center no-repeat;
      background-size: cover;
      width: 396px;
      height: 545px;
      margin-left: 127px;
      @media screen and (max-width: 1024px) {
        display: none;
      }
    }
  }
}
